  export default "import React from 'react'\nimport { Graph } from '@antv/x6'\nimport './index.less'\n\nGraph.registerNode(\n  'custom-text',\n  {\n    markup: [\n      {\n        tagName: 'ellipse',\n        selector: 'e',\n      },\n      {\n        tagName: 'rect',\n        selector: 'r',\n      },\n      {\n        tagName: 'circle',\n        selector: 'c',\n      },\n      {\n        tagName: 'text',\n        selector: 'label',\n      },\n      {\n        tagName: 'rect',\n        selector: 'outline',\n      },\n    ],\n    attrs: {\n      label: {\n        textAnchor: 'middle',\n        textVerticalAnchor: 'middle',\n        fontSize: 48,\n      },\n      e: {\n        stroke: '#8f8f8f',\n        strokeWidth: 1,\n        fill: 'rgba(255,0,0,0.3)',\n      },\n      r: {\n        stroke: '#8f8f8f',\n        strokeWidth: 1,\n        fill: 'rgba(0,255,0,0.3)',\n      },\n      c: {\n        stroke: '#8f8f8f',\n        strokeWidth: 1,\n        fill: 'rgba(0,0,255,0.3)',\n      },\n      outline: {\n        ref: 'label',\n        refX: 0,\n        refY: 0,\n        refWidth: '100%',\n        refHeight: '100%',\n        stroke: '#8f8f8f',\n        strokeWidth: 1,\n        strokeDasharray: '5 5',\n        strokeDashoffset: 2.5,\n        fill: 'none',\n      },\n    },\n  },\n  true,\n)\n\nexport default class Example extends React.Component {\n  private container: HTMLDivElement\n\n  componentDidMount() {\n    const graph = new Graph({\n      container: this.container,\n      background: {\n        color: '#F2F7FA',\n      },\n      interacting: false,\n    })\n\n    const node = graph.addNode({\n      shape: 'custom-text',\n      x: 320,\n      y: 160,\n      width: 280,\n      height: 120,\n      attrs: {\n        label: {\n          text: 'H',\n        },\n        e: {\n          ref: 'label',\n          refRx: '50%',\n          refRy: '25%',\n          refCx: '50%',\n          refCy: 0,\n          refX: '-50%',\n          refY: '25%',\n        },\n        r: {\n          ref: 'label',\n          refX: '100%',\n          refY: '100%',\n          x: -10,\n          y: -10,\n          refWidth: '50%',\n          refHeight: '50%',\n        },\n        c: {\n          ref: 'label',\n          refRCircumscribed: '50%',\n        },\n      },\n    })\n\n    let currentTransitions = 0\n    let typeToggle = true\n\n    function type() {\n      node.transition('attrs/label/text', 'Hello, World!', {\n        delay: 1000,\n        duration: 4000,\n        interp(start, end) {\n          return function (time) {\n            return start + end.substr(1, Math.ceil(end.length * time))\n          }\n        },\n      })\n\n      typeToggle = false\n    }\n\n    function untype() {\n      node.transition('attrs/label/text', 'H', {\n        delay: 1000,\n        duration: 4000,\n        timing(time) {\n          return 1 - time\n        },\n        interp(start, end) {\n          return function (time) {\n            return end + start.substr(1, Math.ceil(start.length * time))\n          }\n        },\n      })\n\n      typeToggle = true\n    }\n\n    type()\n\n    node.on('transition:start', () => {\n      currentTransitions += 1\n    })\n\n    node.on('transition:complete', () => {\n      currentTransitions -= 1\n\n      if (currentTransitions === 0) {\n        if (typeToggle) {\n          type()\n        } else {\n          untype()\n        }\n      }\n    })\n  }\n\n  refContainer = (container: HTMLDivElement) => {\n    this.container = container\n  }\n\n  render() {\n    return (\n      <div className=\"attrs-ref-elem-app\">\n        <div className=\"app-content\" ref={this.refContainer} />\n      </div>\n    )\n  }\n}\n";;
  