  export default "import React from 'react'\nimport { Graph, Interp } from '@antv/x6'\nimport './index.less'\n\nGraph.registerNode(\n  'custom-rect',\n  {\n    markup: [\n      {\n        tagName: 'ellipse',\n        selector: 'e',\n      },\n      {\n        tagName: 'rect',\n        selector: 'r',\n      },\n      {\n        tagName: 'circle',\n        selector: 'c',\n      },\n      {\n        tagName: 'rect',\n        selector: 'outline',\n      },\n    ],\n    attrs: {\n      e: {\n        stroke: '#8f8f8f',\n        strokeWidth: 1,\n        fill: 'rgba(255,0,0,0.3)',\n      },\n      r: {\n        stroke: '#8f8f8f',\n        strokeWidth: 1,\n        fill: 'rgba(0,255,0,0.3)',\n      },\n      c: {\n        stroke: '#8f8f8f',\n        strokeWidth: 1,\n        fill: 'rgba(0,0,255,0.3)',\n      },\n      outline: {\n        refX: 0,\n        refY: 0,\n        refWidth: '100%',\n        refHeight: '100%',\n        stroke: '#8f8f8f',\n        strokeWidth: 1,\n        strokeDasharray: '5 5',\n        strokeDashoffset: 2.5,\n        fill: 'none',\n      },\n    },\n  },\n  true,\n)\n\nexport default class Example extends React.Component {\n  private container: HTMLDivElement\n\n  componentDidMount() {\n    const graph = new Graph({\n      container: this.container,\n      background: {\n        color: '#F2F7FA',\n      },\n      interacting: false,\n    })\n\n    const node = graph.addNode({\n      shape: 'custom-rect',\n      x: 160,\n      y: 100,\n      width: 280,\n      height: 120,\n      attrs: {\n        e: {\n          refRx: '50%',\n          refRy: '25%',\n          refCx: '50%',\n          refCy: 0,\n          refX: '-50%',\n          refY: '25%',\n        },\n        r: {\n          refX: '100%',\n          refY: '100%',\n          refWidth: '50%',\n          refHeight: '50%',\n          x: -10, // additional x offset\n          y: -10, // additional y offset\n        },\n        c: {\n          refRCircumscribed: '50%',\n          refCx: '50%',\n          refCy: '50%',\n        },\n      },\n    })\n\n    let stretchToggle = false\n    let currentTransitions = 0\n\n    function contract() {\n      node.transition(\n        'size',\n        { width: 40, height: 40 },\n        {\n          delay: 1000,\n          duration: 4000,\n          interp: Interp.object,\n        },\n      )\n\n      node.transition(\n        'position',\n        { x: 280, y: 130 },\n        {\n          delay: 1000,\n          duration: 4000,\n          interp: Interp.object,\n        },\n      )\n\n      stretchToggle = true\n    }\n\n    function stretch() {\n      node.transition(\n        'size',\n        { width: 280, height: 120 },\n        {\n          delay: 1000,\n          duration: 4000,\n          interp: Interp.object,\n        },\n      )\n\n      node.transition(\n        'position',\n        { x: 160, y: 100 },\n        {\n          delay: 1000,\n          duration: 4000,\n          interp: Interp.object,\n        },\n      )\n\n      stretchToggle = false\n    }\n\n    stretch()\n\n    node.on('transition:start', () => {\n      currentTransitions += 1\n    })\n\n    node.on('transition:complete', () => {\n      currentTransitions -= 1\n\n      if (currentTransitions === 0) {\n        if (stretchToggle) {\n          stretch()\n        } else {\n          contract()\n        }\n      }\n    })\n  }\n\n  refContainer = (container: HTMLDivElement) => {\n    this.container = container\n  }\n\n  render() {\n    return (\n      <div className=\"attrs-ref-node-app\">\n        <div className=\"app-content\" ref={this.refContainer} />\n      </div>\n    )\n  }\n}\n";;
  